body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.square {
  height: 100px;
  width: 100px;
  border: 1px solid black;
  font-size: 90px;
  text-align: center;
}

.board {
  height: 300px;
  width: 300px;
  border: 1px solid black;
  position: fixed;
  top: 7%;
  left: 40%;

}

.row {
  width: 100%;
  height: 33.33%;
  display: -webkit-flex;
  display: flex;
}

.game {
  height: 400px;
  width: 400px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0 auto
  
}

.status {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding-top: 10px;
  position: fixed;
  bottom: 43%;
  left: 46%
}

.msg{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: fixed;
  left: 44%;
  

}
